.contenedor-contacto {
  text-align: center;
  padding: 10px;
  color: var(--color-fuente);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.contacto-titulo {
  display: flex;
  flex-flow: row nowrap;
  gap: 3px;
  justify-content: center;
  width: 100%;
  height: 10%;
}

.contacto {
  height: 15%;
  width: 50%;
  display: flex;
  flex-direction: column;
  margin: auto;
}

.contacto-devs-titulo {
  margin: 20px auto;
  font-size: 25px;
}

.contacto-contenedor-items {
  display: flex;
}

.contacto-item {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.contacto-nombre {
  font-size: 20px;
  font-weight: bold;
}

.contacto-redes {
  display: flex;
  width: 156px;
  height: 100%;
  margin: auto;
  align-items: center;
  justify-content: space-around;
}


.link {
  display: flex;
  height: 40px;
  width: 40px;
}

.link img {
  height: auto;
  width: auto;
  pointer-events: none;
  cursor: default;
}

@media only screen and (max-width: 726px) {

  .contacto {
    height: 30%;
    width: 100%;
  }

  .contacto-contenedor-items {
    flex-direction: column;

  }

  .contacto-item {
    margin: 10px auto;
    width: 100%;
  }

  .contacto-redes {}

}